import { Box, Text } from "@chakra-ui/react";

const Footer = () => {
  return (
    <Box
      bg="#C2C2BC"
      className="subhedding"
      color={"black"}
      textAlign="center"
      p="20px"
      fontSize="sm"
    >
      <Text>
        Made with  and  by Dnyaneshwari Pandhare
        <span role="img" aria-label="Emoji">
          
        </span>
      </Text>
      <Text mt="10px">
         {new Date().getFullYear()} All rights reserved to Dnyaneshwari
        Pandhare
      </Text>
    </Box>
  );
};

export default Footer;
